/* Main.module.css */
.formContainer {
    background-color: var(--primary-bg-color);
    color: var(--primary-text-color);
    padding: var(--default-padding);
    border-radius: var(--border-radius);
    margin-left: 250px;
    margin-top: 10px;
}

.inputField {
    background-color: #2c2c2c; /* Darker background for input fields for better contrast */
    color: #f4e4a2; /* Using the soft yellow for input text color */
    padding: 10px;
    margin: 5px 0; /* Space out inputs */
    border: 1px solid #555; /* Darker border for inputs */
    border-radius: 8px;
    width: calc(100% - 20px); /* Adjust width to account for padding */
}

td {
    text-align: center;
    padding: 10px; /* Add some padding to table cells for spacing */
}

th {
    color: #f4e4a2;
    padding: 10px; /* Consistent padding with td for alignment */
}
