:root {
    --primary-bg-color: #1a1a1a;
    --primary-text-color: #f4e4a2;
    --header-bg-color: #ff4242;
    --header-text-color: #fff;
    --footer-bg-color: #212121;
    --footer-text-color: #f4e4a2;
    --sidebar-bg-color: #313131;
    --button-bg-color: #00ffab;
    --button-hover-bg-color: #00856f;
    --font-family: 'Roboto', sans-serif;
    --default-padding: 20px;
    --default-margin: 10px;
    --sidebar-width: 200px;
    --border-radius: 8px;
    --hover-transition: all 0.3s ease;
    --box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.aboutSection {
    margin-top: 10px;
    background-color: var(--primary-bg-color);
    color: var(--primary-text-color);
    padding: var(--default-padding);
    border-radius: var(--border-radius);
    margin-left: 250px;
}

.aboutTable {
    width: 100%;
    margin-top: var(--default-margin);
    border-collapse: collapse;
}

    .aboutTable td, .aboutTable th {
        padding: var(--default-margin);
        border: 1px solid var(--primary-text-color); /* Optional: adds a subtle border using the text color */
    }

    /* Optional: If you want to style the table headers differently */
    .aboutTable th {
        background-color: var(--sidebar-bg-color);
        color: var(--header-text-color);
    }
